  overflow-x: auto; /* Th√™m thanh cu·ªôn ngang khi c·∫ßn */
  overflow-y: visible; /* ƒê·∫£m b·∫£o kh√¥ng b·ªã khu·∫•t theo chi·ªÅu d·ªçc */
}

.cart-container h1 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 18px;
}

.cart-table {
  width: 100%;
  border-collapse: collapse;
  table-layout: auto; /* Cho ph√©p b·∫£ng t·ª± ƒë·ªông ƒëi·ªÅu ch·ªânh k√≠ch th∆∞·ªõc */
}

/* Responsive Table */
.cart-table th,
.cart-table td {
  text-align: center;
  padding: 10px;
  border-bottom: 1px solid #ccc;
  font-size: 14px; /* Gi·∫£m k√≠ch th∆∞·ªõc ch·ªØ */
}

.cart-table img {
