#checkout-all {
  background: #28a745;
  padding: 10px 20px;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  font-size: 16px;
}

#checkout-all:hover {
  background: #218838;
}

.buy-button,
.remove-button {
  background: #007bff; 
  color: white; 
  border: none;
  padding: 5px 8px; 
  border-radius: 3px; 
  font-size: 12px; 
  cursor: pointer;
}

.buy-button:hover {
  background: #0056b3;
}

.remove-button {
  background: #dc3545;
}

.remove-button:hover {
  background: #bd2130;
}

.close-button {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close-button:hover,
.close-button:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

#order-form input,
#order-form button {
  width: 95%;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
}

#order-form button {
  background-color: #28a745;
  color: white;
  border: none;
  cursor: pointer;
}

#order-form button:hover {
  background-color: #218838;
}

/* CSS cho ƒë∆°n v·ªã ti·ªÅn t·ªá nh·ªè */
.currency {
