:root {
  --main-font: sans-serif;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: background-color 0.5s, color 0.5s;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body.lang-en {
  --main-font: 'Poppins', sans-serif;
}

body.lang-vi {
  --main-font: 'Be Vietnam Pro', sans-serif;
}

* {
  font-family: var(--main-font, sans-serif); // fallback l√† sans-serif
}

body.light-mode {
  background: linear-gradient(90deg, #e2e2e2, #c9d6ff);
  color: #000;

  .nav {
    background: linear-gradient(135deg, #B0E0E6, #2E8B57);

    .linkPage a:hover {
      animation: glowEffect-light 0.3s ease-in-out;
    }
  }

  .user-menu .dropdown li a {
    color: #000;
  }

  .social {
    .link-fb {
      color: #4267B2;
    }

    .link-twit {
      color: #1DA1F2;
    }

    .link-ig {
      color: #E1306C;
    }

    .link-gg {
      color: #DB4437;
    }
  }

  .home-page {
    .home-odd-floor {
      background-color: #d1fae5;
    }

    .home-even-floor {
      background-color: #fff;
    }

    .title {
      color: #1f2937;
    }

    .sub-title {
      color: #4b5563;
    }

    .image-gallery-container {
      .tag {
        background: rgba(255, 255, 255, 0.7);
      }

      .tag-title {
        color: #2c3e50;
      }

      .tag-desc {
        color: #4b5563;
      }
    }
  }

  // Travel Page
  .nice-place,
  .tour-summer {
    background-color: #d6e6f5;

    .tour-summer-content-item {
      background-color: #fff;
    }
  }

  .service-item {
    background: #e1ffe5;
    box-shadow: 0 1px 8px #185a9d;

    &:hover {
      box-shadow: 0 2px 12px #165da5;
      background: #cefcce;
      transform: translateY(-6px) scale(1.03);
    }
  }

  .about,
  .review {
    background-color: #fff;
  }

  .products-page {
    .item {
      background-color: #fff;
    }
  }

  .product-detail {
    background-color: rgba(255, 255, 255, 0.8);

    .quantity-control button {
      background-color: rgba(255, 255, 255, 0.8);
    }

    .quantity-control input {
      background-color: rgba(255, 255, 255, 0.8);
    }

    .modal-product-detail-content {
      background-color: rgba(255, 255, 255, 1);

      .cart-label {
        color: #374151;
      }
    }
  }

  .cart-container {
    background-color: rgba(255, 255, 255, 0.8);

    .modal-cart-content {
      background-color: rgba(255, 255, 255, 1);

      .cart-label {
        color: #374151;
      }
    }
  }

  .detail {
    h2 {
      color: #333;
    }
  }
}

body.dark-mode {
  background: linear-gradient(90deg, #313131, #2B3A5A);
  color: #fff;

  .nav {
    background: linear-gradient(135deg, #0F4146, #2D8857);

    .linkPage a:hover {
      color: #9FBFAA;
      background: linear-gradient(135deg, #0F4146, #2D8857);
      animation: glowEffect-dark 0.3s ease-in-out;
    }

    ul li a.active {
      background: linear-gradient(135deg, #0F4146, #2D8857);
    }
  }

  .login-register-container {
    .form-box {
      background: #121212;

      h1 {
        color: #DFDFDF;
      }

      .input-box input,
      .input-box .forgot-link {
        color: #77777A;
        background: #282828;
      }

      .btn {
        background: #1C604D;
      }

      p {
        color: #DFDFDF;
      }

      .social-icons {
        a {
          color: #DFDFDF;
          border: 2px solid #787878;
        }
      }
    }

    .toggle-box {
      background-color: #121212;
      overflow: hidden;
    }

    .toggle-box::before {
      background: #1C604D;
    }
  }

  .user-menu {
    .dropdown {
      background-color: #1e1e1e;

      li:hover {
        background: #262626;
      }
    }
  }

  .language-selector {
    .lang-dropdown {
      background: #1e1e1e;
    }
  }

  .profile-container {
    background-color: #121212;

    h2 {
      color: #99CCBD;
    }

    .right-panel input,
    .right-panel textarea {
      color: #fff;
      background: #3B3B3B;
      border-color: #787878;
    }

    .btn {
      border: solid #5A786F 2px;
    }

    .btn:hover {
      background: #121212;
      color: #5A786F;
      box-shadow: 0 0 16px rgba(46, 139, 87, 0.8);
    }

    .social {
      .link-fb {
        i {
          color: #4267B2;
        }

        span {
          color: #82A0F1;
        }
      }

      .link-twit {
        i {
          color: #1DA1F2;
        }

        span {
          color: #0079C6;
        }
      }

      .link-ig {
        i {
          color: #E1306C;
        }

        span {
          color: #F6487D;
        }
      }

      .link-gg {
        i {
          color: #DB4437;
        }

        span {
          color: #EE5645;

        }
      }
    }
  }

  .sidebar-links {
    a {
      color: #BAC5FF;
    }
  }

  .home-page {

    #prev,
    #next {
      background-color: rgba(0, 0, 0, 0.5);

      &:hover {
        background-color: rgba(0, 0, 0, 0.2);
      }
    }

    .home-odd-floor {
      background-color: #131C20;
    }

    .home-even-floor {
      background-color: #11191D;
    }

    .title {
      color: rgba(255, 255, 255, 0.8);
    }

    .sub-title {
      color: rgba(255, 255, 255, 0.6);
    }

    .image-gallery-container {
      .tag {
        background: rgba(0, 0, 0, 0.5);
      }

      .tag-title {
        color: rgba(255, 255, 255, 0.8);
      }

      .tag-desc {
        color: rgba(255, 255, 255, 0.6);
      }
    }

    .destination-image {
      opacity: 90%;
    }
  }

  // Travel Page
  .nice-place,
  .tour-summer {
    background-color: #1a2733;

    .tour-summer-content-item {
      background-color: #111827;
    }
  }

  .service-item {
    background: #225a43;
    box-shadow: 0 1px 8px #0f4a74;

    &:hover {
      background: #317156;
      box-shadow: 0 2px 12px #0d5a8a;
    }

    p {
      color: #b8ffcd; // nh·∫π, n·ªïi b·∫≠t tr√™n n·ªÅn t·ªëi
    }
  }

  .about,
  .review {
    background-color: #111827;
  }

  .products-page {
    .item {
      background-color: #fff;
    }
  }

  .products-page {
    #search-input {
      background-color: #131C20;
    }

    .item {
      background-color: #11191D;
    }

    .detail {
      h2 {
        color: rgba(255, 255, 255, 0.6);
      }
    }

    .modal-content {
      background-color: #11191D;

      .cart-input {
        background-color: #1e272b;
      }
    }
  }

  .product-detail {
    background-color: #11191D;

    .quantity-control button {
      background-color: #11191D;
    }

    .quantity-control input {
      background-color: #11191D;
    }

    .modal-product-detail-content {
      background-color: #11191D;

      .cart-input {
        background-color: #1e272b;
      }
    }
  }

  .cart-container {
    background-color: #11191D;

    .cart-table td {
      input {
        background-color: #11191D;
      }
    }

    .modal-cart-content {
      background-color: #11191D;

      .cart-input {
        background-color: #1e272b;
      }
    }
  }

  .news-container {
    .create-post-box {
      background-color: #11191D;

      .create-post-input {
        color: rgba(255, 255, 255, 0.8);
        background-color: #1e272b;
      }
    }

    .modal-overlay {
      .post-creator-popup {
        background-color: #11191D;

        .post-creator-body textarea {
          background-color: #1e272b;
        }

        .post-creator-footer .attachment-options .add-photo-btn {
          background-color: #232b2f;
        }
      }
    }

    .post-list {
      .post {
        background-color: #11191D;

        .share-popup {
          background-color: #11191D;

          .share-option {
            background-color: #1e272b;

            &:hover {
              background-color: #323f45;
            }
          }
        }
      }

      .post-content {
        color: rgba(255, 255, 255, 0.6);
      }

      .post-actions {
        button {
          color: rgba(255, 255, 255, 0.8);

          &:hover {
            background-color: #1e272b;
          }
        }
      }

      .post-comments {
        .comment-input {
          background-color: #1e272b;
        }

        .comment-content-wrapper {
          background-color: #232b2f;

          .comment-author {
            color: #fff;
          }

          .comment-content {
            color: rgba(255, 255, 255, 0.8);
          }
        }
      }
    }
  }
}

/* Hi·ªáu ·ª©ng √°nh s√°ng (Glow) khi hover cho navbar */
@keyframes glowEffect-light {
  0% {
    box-shadow: 0 0 8px #B0E0E6, 0 0 15px #2E8B57;
    /* √Ånh s√°ng nh·∫π t·ª´ gradient */
  }

  50% {
    box-shadow: 0 0 12px #B0E0E6, 0 0 20px #2E8B57;
  }

  100% {
    box-shadow: 0 0 8px #B0E0E6, 0 0 15px #2E8B57;
  }
}

@keyframes glowEffect-dark {
  0% {
    box-shadow: 0 0 8px #0F4146, 0 0 15px #2E8B57;
    /* √Ånh s√°ng nh·∫π t·ª´ gradient */
  }

  50% {
    box-shadow: 0 0 12px #0F4146, 0 0 20px #2E8B57;
  }

  100% {
    box-shadow: 0 0 8px #0F4146, 0 0 15px #2E8B57;
  }
}