:relative;box-shadow:0 2px 24px #185a9d;animation:modalShow .3s}@keyframes modalShow{from{transform:translateY(-40px);opacity:0}to{transform:translateY(0);opacity:1}}.close-btn{color:#185a9d;position:absolute;top:14px;right:18px;font-size:28px;font-weight:bold;cursor:pointer;transition:color .2s}.close-btn:hover{color:#f33}@media(max-width: 1200px){.container{max-width:98vw;padding:0 10px}.footer-container{max-width:98vw}}@media(max-width: 1024px){.header-content{width:95vw;min-height:unset;border-radius:0 0 2rem 2rem;padding:40px 16px}.nice-place-item,.service-item{width:45%}.about-content-left,.about-content-right,.tour-summer-content-item-img,.tour-summer-content-item-text{width:100%;padding:0}.about-content-right{padding-left:0}.review-item{width:48%;margin-bottom:20px}}@media(max-width: 768px){.header-content{width:100vw;min-width:0;padding:36px 3vw 30px 3vw}.nice-place{margin-top:170px}.nice-place-item,.service-item{width:100%;margin-bottom:22px}.row{flex-direction:column;align-items:center}.about-content-right{margin-top:18px}.tour-summer-content-item{flex-direction:column !important}.footer-container{flex-direction:column;align-items:center}.footer-box{width:100%;text-align:center}.review-item{width:98%;margin-bottom:22px}}@media(max-width: 480px){.travel-form{padding:16px 5px}.travel-form h1{font-size:18px}.travel-form button{width:100%;font-size:1rem}.header-content>h1{font-size:20px}.header-content>p{font-size:15px}.nice-place-item-text h2{font-size:19px}.nice-place-item-text p{font-size:13px}.tour-summer-content-item-text h2{font-size:16px}.tour-summer-content-item-text p{font-size:13px}}