#background-video{position:fixed;top:0;left:0;width:100%;height:100%;object-fit:cover;z-index:-1}.search-bar{position:fixed;top:90px;left:50%;transform:translateX(-50%);width:100%;z-index:1;max-width:800px;text-align:center}#search-input{width:100%;max-width:600px;padding:15px;border:2px solid #ccc;border-radius:25px;font-size:16px;box-shadow:0px 2px 5px rgba(0,0,0,.1)}#search-input:focus{outline:none;border-color:#066;box-shadow:0px 2px 5px rgba(0,102,102,.5)}@media(max-width: 768px){.search-bar{padding:0 15px;max-width:60%}#search-input{font-size:14px;padding:12px 16px}}.product-grid{display:grid;gap:30px;padding:100px 20px;grid-template-columns:repeat(4, 1fr)}@media(max-width: 1200px){.product-grid{grid-template-columns:repeat(3, 1fr)}}@media(max-width: 900px){.product-grid{grid-template-columns:repeat(2, 1fr)}}@media(max-width: 600px){.product-grid{grid-template-columns:1fr}}.item{display:flex;flex-direction:column;justify-content:space-between;border-radius:15px;box-shadow:0 4px 8px rgba(0,0,0,.1);text-align:center;padding:20px;transition:transform .3s ease,box-shadow .3s ease;border:5px solid #2ecc71;height:100%;width:100%;max-width:100%;box-sizing:border-box}.item:hover{transform:translateY(-5px);box-shadow:0 8px 15px rgba(0,0,0,.2)}.image-box{flex:1;display:flex;align-items:center;justify-content:center}.image-box img{max-width:100%;max-height:150px;object-fit:contain;display:block}.details{margin-top:auto;padding-top:15px}.details h2{font-size:18px;font-weight:bold;margin-bottom:10px}.details h2 span{display:block;font-size:14px;color:#888;margin-top:5px}.products-page .buttons{display:flex;gap:10px;margin-top:15px;justify-content:center}.products-page .btn{display:flex;align-items:center;justify-content:center;font-size:16px;font-weight:bold;border:none;border-radius:25px;cursor:pointer;text-decoration:none;color:#fff;transition:background-color .3s ease,transform .2s ease}.btn-info{background:#066}.btn-info:hover{background:#004d4d;transform:scale(1.05)}.btn-buy{background:#2ecc71;display:flex;align-items:center;justify-content:center;gap:5px}.btn-buy:hover{background:#27ae60;transform:scale(1.05)}.btn-buy::after{content:"üõí";font-size:18px}.item{border-radius:30px}.cart-button{position:fixed;top:100px;right:30px;font-size:32px;background:none;border:none;color:#fff;text-decoration:none;cursor:pointer;z-index:1}.modal{display:flex;position:fixed;z-index:1000;left:0;top:0;width:100%;height:100%;overflow:auto;background-color:rgba(0,0,0,.4)}.modal-content{background-color:#fff;margin:15% auto;padding:20px;border:1px solid #888;width:80%;max-width:400px;border-radius:10px}.close-button{color:#aaa;float:right;font-size:28px;font-weight:bold;cursor:pointer}.close-button:hover{color:#000}.buy-now-form label{display:block;margin:10px 0 5px}.buy-now-form input,.buy-now-form select,.buy-now-form button{width:100%;padding:10px;margin-bottom:15px;border:1px solid #ccc;border-radius:5px}.buy-now-form button{background-color:#28a745;color:#fff;cursor:pointer}.buy-now-form button:hover{background-color:#218838}